import React, { Component } from 'react';
import { Row, Col, Modal, Button } from 'react-bootstrap';
import CardNewsComponent from './card-news/card-news.component';
import { connect } from '../../imports/react-redux.import';
import { getNews } from '../../core/actions/news.actions';
import FormNewsComponent from './form-news/form-news.component';
import { NewsModel } from '../../core/models/news.model';
import { ShakeLittle, ShakeSlow, ShakeHorizontal, Shake } from '../../imports/reshake.import';

class HomeView extends Component<any,any> {
    
  constructor(props: any) {
    super(props);

    this.state = {
      show: false,
      shake: false
    };
  }

  componentDidMount() {
    this.props.getNews();
  }

  render() {
    const { userData ,news } = this.props;

    return (
      <>
        {/*<Row>
          <Col md={ 12 } className="text-center">
            <CardNewsComponent 
              role={ userData.role }
              news={ news }
            />
          </Col>
        </Row>*/}
        
        <Modal 
          show={ this.state.show }
          size="lg"
          centered
        >
          <FormNewsComponent
            initialValues=""
            submitActions={(formData: NewsModel) => {
              console.log(formData);
            }}
            cancel={ () => {} }
          />
        </Modal>
        <Button onClick={ () => { this.setState({ show: true }) } }>Modal</Button>
        <div>
        <ShakeHorizontal 
          h={60}
          v={0}
          r={0}
          dur={1000}
          int={30.5}
          max={100}
          fixed={true}
          fixedStop={true}
          freez={false}
          active={this.state.shake}
          >
            Brrr...
        </ShakeHorizontal>
        </div>

        <Button
        onClick={() => window.setTimeout(() => this.setState({shake: true}), 2000) }
        ></Button>
      </>
    );
  }
}

const mapDispatchToProps = (dispatch: Function) => ({
  getNews: () => dispatch(getNews())
});

const mapStateToProps = (state: any) => ({ 
  userData: state.userData,
  news: state.news
});

export default connect(mapStateToProps,mapDispatchToProps)(HomeView);